import {Component, OnInit} from '@angular/core';
import {RouteConfig, RouterLink, ROUTER_DIRECTIVES, RouteParams} from '@angular/router-deprecated';
import {GeneralComponent} from './general/general.component';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/operator/switchMap';
import {ToasterContainerComponent, ToasterConfig} from 'angular2-toaster/angular2-toaster';
import {MenuListComponent} from "./general/components/MenuList/MenuListComponent";

@Component({
    selector: 'app',
    templateUrl: './app/app.html',
    directives: [RouterLink, ROUTER_DIRECTIVES, MenuListComponent, ToasterContainerComponent]
})

@RouteConfig([
    { path: '/:orderId/...', name: 'General', component: GeneralComponent }
])
export class AppComponent implements OnInit {
    
    ngOnInit() {
    }
    id: string;
    orderId: string;
    toasterConfig = new ToasterConfig({ limit: 1 });
}